.chatContainer {
    transform: translateX(-7vw);
    display: flex;
    flex-direction: row;
    height:80vh;
    width:67vw;
    column-gap: 2vw;
}

.mainChatSpace {
    flex-grow: 7;
    display:flex;
    flex-direction: column;
    align-items: stretch;
    background-color: aliceblue;
    border-radius: 50px;
    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
    
}

.chatSpace {
    height:92%;
    width:100%;
}



.chatOnline {
    flex-grow: 1;
   
}
.chatWrite button{
    color:whitesmoke;
    font-weight: 700;
}