.memberList {
  position: fixed;
  inset: 0px 40px;
  left: 61vw;
  background-color: aliceblue;
  height: 80vh;
  width: 80px;
  border-left: 10px solid aliceblue;
  border-radius: 50px;
  box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset,
    rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
  overflow: hidden;
  transition: 1s;
}
.memberList.active {
  transition: 1s;
  width: 20vw;
}

.memberList.active .toggle::before {
  transform: translateY(0px) rotate(-405deg);
}
.memberList.active .toggle::after {
  transform: translateY(0px) rotate(225deg);
}
.logoutButton {
  cursor: pointer;
}

.memberListItem {
  display: flex;
  justify-content: left;
  align-items: center;
  height: 6vh;
  width: 10vw;
  padding: 1vh 2vw 2vh 0;
  border-radius: 3vh;
  margin-bottom: 1vh;
}
.memberListUsername {
  position: relative;
  display: block;
  padding: 0 10px;
  height: 60px;
  line-height: 60px;
  text-align: start;
  white-space: nowrap;
  color: #144c9af7;
  font-weight: 700;
  font-family: blu;
}
.logoutTitle {
  position: relative;
  display: block;
  padding: 0 28px;
  height: 60px;
  line-height: 60px;
  text-align: start;
  white-space: nowrap;
  color: #144c9af7;
  transition: 1s;

  font-weight: 700;
  font-family: blu;
}

.toggle {
  position: absolute;
  width: 50px;
  height: 50px;
  background: #144c9af7;
  bottom: 93px;
  right: 230px;
  border-radius: 50%;
  box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset,
    rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.toggle::before {
  content: "";
  position: absolute;
  width: 26px;
  height: 3px;
  border-radius: 3px;
  background: aliceblue;
  transform: translateY(-5px);
  transition: 1s;
}
.toggle::after {
  content: "";
  position: absolute;
  width: 26px;
  height: 3px;
  border-radius: 3px;
  background: aliceblue;
  transform: translateY(5px);
  transition: 1s;
}

.lastLi {
  color: transparent;
}

.memberList ul {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.memberList ul li {
  position: relative;
  list-style: none;
  width: 100%;
  /* background: #144c9af7; */
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #333;
}
.logoutButton {
  width: 300px;
  margin-top: 20px;
  margin-bottom: 10px;
  transition: 0.2s;
  display: flex;
  align-items: center;
  padding-left: 1vw;
}

.logoutButton:hover {
  background: #144c9af7;
  color: aliceblue !important;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  border-top-right-radius: -30px;
  border-bottom-right-radius: -30px;
}

.memberListAvatar {
  position: relative;
  display: block;
  min-width: 60px;
  height: 60px;
  text-align: center;
}

.logoutIcon svg {
  height: 30px;
  width: auto;
  color: #144c9af7 !important;
  transition: 0.8s;
}

.logoutButton:hover .logoutIcon svg {
  color: aliceblue !important;
}

.logoutButton:hover .logoutTitle {
  color: aliceblue !important;
}
